import { t } from 'i18next';
import { useNavigate } from 'react-router';
import { useCallback, useMemo, useState } from 'react';
import { Icon } from '@/components/Icon';
import { breakpoints } from '@/constants/devices';
import ROUTES from '@/constants/routes';
import { EventsStatus } from '@/apollo/generated/types';
import { TSpan } from '@/components/Typography';
import { SubscriptionType } from '@/screens/SocialHall/types';
import {
  useCameraPermissionListener,
  useMicrophonePermissionListener,
  useMediaPermissionPopup,
  useCheckIfEventHasSocialHallAttendees,
} from '@/features/socialHall/hooks';
import { useError } from '@/hooks/useError';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useAuth } from '@/features/auth/contexts';
import { useWindowDimensions } from '@/hooks/dimensions';
import {
  useSocialHallContext,
  useSocialHallCallContext,
  useSocialHallEventContext,
} from '@/providers';
import {
  ControlPanelIcon,
  ControlPanelWrapper,
  ControlPanelMainContainer,
} from './styles';
import {
  attendeeLeaveEventModalData,
  hostFinishInstantEventModalData,
  hostFinishMainEventModalData,
} from '../EventModals/EventModal/data';
import { EventModal } from '../EventModals/EventModal';
import { MuteControl } from './MuteControl';
import { VideoControl } from './VideoControl';
import { ScreenSharingControl } from './ScreenSharingControl';
import { RaiseHandControl } from './RaiseHandControl';
import { ChatControl } from './ChatControl';
import { type ControlPanelProps } from './types';
import { MemberControl } from './MemberControl';
import { MediaSettingControl } from './MediaSettingControl';
import { MediaPermissionNotification } from '../MediaPermissionNotification';
import { CopyLinkControl } from './CopyLinkControl';

export const ControlPanel = ({
  onToggleChat,
  onToggleMember,
  isNewMessage,
  showChatPanel,
  isReconnecting,
  showMembersPanel,
}: ControlPanelProps) => {
  const [hasVideoPermission] = useCameraPermissionListener();
  const [hasAudioPermission] = useMicrophonePermissionListener();
  const { onTogglePopup, showPermissionPopup } = useMediaPermissionPopup();

  const navigate = useNavigate();
  const { logError } = useError();
  const { flags } = useLaunchDarkly();
  const { isUnregistered } = useAuth();
  const { width } = useWindowDimensions();
  const [, setLoading] = useState(false);
  const {
    isMuted,
    onLeaveCall,
    toggleCamera,
    toggleMuteCall,
    isCameraEnable,
    localVideoTrack,
    onSendSubscriptionMessage,
  } = useSocialHallCallContext();
  const {
    isMainEvent,
    onUpdateEventStatus,
    isEventHost,
    isEventSpeaker,
    isPreEvent,
    isPostEvent,
    eventDetails,
  } = useSocialHallEventContext();
  const isMobile = useMemo(() => width < breakpoints.MOBILE_MAX, [width]);
  const {
    socialHallAttendeesAndGroups,
    activeSocialHallGroup,
    setShowBuzzRoom,
  } = useSocialHallContext();

  const [showFinishModal, setShowFinishModal] = useState<boolean>(false);
  const [showLeaveModal, setShowLeaveModal] = useState<boolean>(false);

  const onLeaveQuietly = useCallback(() => {
    try {
      if (!eventDetails?._id) {
        return;
      }
      if (!isEventHost) {
        setShowLeaveModal(true);
      } else {
        setShowBuzzRoom(false);
        onLeaveCall();
      }
    } catch (err) {
      logError(err, '', false);
    }
  }, [isEventHost, logError, onLeaveCall, setShowBuzzRoom, eventDetails?._id]);

  const onUpdateEvent = useCallback(
    async (status: EventsStatus) => {
      try {
        setLoading(true);
        await onUpdateEventStatus(status);
      } catch (err) {
        logError(err, '');
      } finally {
        setLoading(false);
      }
    },
    [logError, onUpdateEventStatus],
  );

  const handleLeaveEvent = useCallback(async () => {
    await onLeaveCall();
    setShowLeaveModal(false);
    navigate(isUnregistered ? ROUTES.GUEST_HOME : ROUTES.HOME_NOUM);
  }, [navigate, isUnregistered, onLeaveCall]);

  const onFinishEvent = useCallback(async () => {
    setShowFinishModal(false);
    onSendSubscriptionMessage({
      type: SubscriptionType.UPDATE_EVENT_STATUS,
      data: EventsStatus.Cancelled,
    });
    const status = eventDetails?.recurring
      ? EventsStatus.Expired
      : EventsStatus.Cancelled;
    await onUpdateEvent(status);
    await onLeaveCall();
  }, [
    onUpdateEvent,
    onSendSubscriptionMessage,
    eventDetails?.recurring,
    onLeaveCall,
  ]);

  const isNotAMainEvent = useMemo(
    () => isPostEvent || isPreEvent,
    [isPostEvent, isPreEvent],
  );

  const isMainEventSpeaker = useMemo(
    () => isMainEvent && (isEventHost || isEventSpeaker),
    [isMainEvent, isEventHost, isEventSpeaker],
  );

  const isMainEventAudience = useMemo(
    () => isMainEvent && !isEventHost && !isEventSpeaker,
    [isMainEvent, isEventHost, isEventSpeaker],
  );

  const hasPublishingMediaPermission = useMemo(
    () => isMainEventSpeaker || isNotAMainEvent,
    [isMainEventSpeaker, isNotAMainEvent],
  );

  const isShowChatControl = useMemo(() => flags.socialHallMessaging, [flags]);

  const isShowRaiseHandControl = useMemo(
    () => isMainEventAudience || isNotAMainEvent,
    [isMainEventAudience, isNotAMainEvent],
  );

  const showLeaveEventBtn = useMemo(
    () => !isMainEvent && !isEventHost && !isMobile,
    [isMainEvent, isEventHost, isMobile],
  );

  const showEndEventBtn = useMemo(
    () =>
      (isMainEvent || eventDetails?.isInstantEvent) &&
      isEventHost &&
      eventDetails?.isInstantEvent &&
      !isMobile,
    [isMainEvent, isEventHost, eventDetails?.isInstantEvent, isMobile],
  );

  const showMemberControl = useMemo(
    () => isMainEvent || eventDetails?.isInstantEvent,
    [isMainEvent, eventDetails?.isInstantEvent],
  );

  const hasSocialHallAttendees = useCheckIfEventHasSocialHallAttendees(
    socialHallAttendeesAndGroups,
    activeSocialHallGroup,
  );

  const endTheMainEventBtnLabel = useMemo(() => {
    if (isMainEvent) {
      return t('noumena.socialhall.end_the_main_event');
    }
    return t('noumena.socialhall.end_the_event');
  }, [isMainEvent]);

  const hostEndEventModalProps = useMemo(
    () =>
      isMainEvent
        ? hostFinishMainEventModalData
        : hostFinishInstantEventModalData,
    [isMainEvent],
  );

  const onToggleMuteCall = () => {
    if (hasAudioPermission) {
      toggleMuteCall();
    } else {
      onTogglePopup('audio');
    }
  };

  const onToggleCamera = () => {
    if (hasVideoPermission) {
      toggleCamera();
    } else {
      onTogglePopup('video');
    }
  };

  return (
    <>
      <ControlPanelWrapper
        data-testid="control_panel_wrapper"
        aria-disabled={isReconnecting}
        disabled={isReconnecting}
      >
        {!isMobile && <MediaSettingControl />}
        <ControlPanelMainContainer>
          {hasPublishingMediaPermission && (
            <>
              <MuteControl
                isMuted={isMuted}
                onToggle={onToggleMuteCall}
                isError={!hasAudioPermission}
              />
              <VideoControl
                onToggle={onToggleCamera}
                isError={!hasVideoPermission}
                isCameraEnable={isCameraEnable}
                isLoading={
                  isCameraEnable &&
                  localVideoTrack?.isPlaying === undefined &&
                  !!hasVideoPermission
                }
              />
              {!isMobile && <ScreenSharingControl />}
            </>
          )}

          {isShowRaiseHandControl && <RaiseHandControl />}
          {isShowChatControl && !hasSocialHallAttendees && (
            <ChatControl
              onToggleChat={onToggleChat}
              isNewMessage={isNewMessage}
              showChatPanel={showChatPanel}
            />
          )}
          {showMemberControl && (
            <MemberControl
              onToggleMemberPanel={onToggleMember}
              showMembersPanel={showMembersPanel}
            />
          )}
          <CopyLinkControl />
        </ControlPanelMainContainer>

        {showLeaveEventBtn && (
          <ControlPanelIcon
            onClick={onLeaveQuietly}
            cursorAllowed
            bgColor="var(--bg-button-danger-primary-default)"
            hoverColor="var(--bg-button-danger-primary-hover)"
            data-title={t('noumena.social_hall.Control_panel.leave')}
          >
            <Icon
              size={24}
              name={eventDetails?._id ? 'logout_m' : 'spinner_m'}
              color="--icon-button-neutral-alt-default"
            />
          </ControlPanelIcon>
        )}
        {showEndEventBtn && (
          <ControlPanelIcon
            onClick={() => setShowFinishModal(true)}
            cursorAllowed
            bgColor="var(--bg-button-danger-primary-default)"
            hoverColor="var(--bg-button-danger-primary-hover)"
          >
            <TSpan colorToken="--text-button-neutral-alt-default">
              {endTheMainEventBtnLabel}
            </TSpan>
          </ControlPanelIcon>
        )}
      </ControlPanelWrapper>
      <EventModal
        isOpen={showFinishModal}
        onClose={() => setShowFinishModal(false)}
        onConfirm={onFinishEvent}
        {...hostEndEventModalProps}
      />
      <EventModal
        isOpen={showLeaveModal}
        onClose={() => setShowLeaveModal(false)}
        onConfirm={handleLeaveEvent}
        isConfirmButtonPrimary
        {...attendeeLeaveEventModalData}
      />
      <MediaPermissionNotification
        onTogglePopup={onTogglePopup}
        showAudioModal={showPermissionPopup.audio}
        showVideoModal={showPermissionPopup.video}
      />
    </>
  );
};
